.achivments-pre-heading {
	font-weight: 600;
	font-size: 11px;
	line-height: 1.4;
	letter-spacing: 0.1em;
	text-transform: uppercase;
	color: $primary-title-color-dk;

	margin-bottom: 20px;
	&::before {
		content: "";
		display: inline-block;
		width: 60px;
		height: 6px;
		border-top: 1px solid $primary-title-color-dk;
		margin-right: 20px;
	}
}

.achivments-title {
	padding-bottom: 20px;
	font-weight: 700;
	font-size: 42px;
	line-height: 1.16;
	letter-spacing: 0.05em;
	color: $primary-title-color-dk;
}
.achivments-text {
	margin-bottom: 64px;
	font-family: Open Sans;
	font-weight: 400;
	font-size: 18px;
	line-height: 1.67;
	letter-spacing: 0.02em;
	color: $primary-text-color-dk;
}
.achivments-item {
	display: flex;
	flex-wrap: wrap;
	margin: -15px;
	& .link {
		background-color: $background-color-dk;
		&:nth-child(2n) {
			position: relative;
			top: 35px;
		}
		@media screen and(max-width:767px) {
			width: calc((100% - 4 * 15px) / 2);
			margin: 15px;
		}
	}
}
.link .list {
	position: relative;
	display: block;
	text-align: center;
	padding: 20px 35px;
	& .list-title {
		display: inline-block;
		font-family: Open Sans;
		font-weight: 700;
		font-size: 42px;
		line-height: 1.35;
		letter-spacing: 0.05em;
		color: $primary-title-color-dk;
	}
	& .list-text {
		margin-top: 6px;
		font-family: Open Sans;
		font-weight: 600;
		font-size: 11px;
		line-height: 1.36;
		color: $primary-title-color-dk;
	}
}
.icon-plus {
	position: absolute;
	top: 27px;
}
